import { Control, Controller } from "react-hook-form"
import { useDatasets } from "@rp/react/web/chat"
import { MenuItem, Select } from "@rp/react/web/mui/core"
import { AutoMode } from "@rp/react/web/mui/icons"
import { CqsScopeIcon } from "./CqsScopeIcon"
import { FormValues } from "./WatchlistSmartGridModal.utils"

function CqsScopeSelector({
  control,
  sourceFilters,
}: {
  control: Control<FormValues>
  sourceFilters: Partial<Record<string, boolean>>
}) {
  const datasets = useDatasets("", true, sourceFilters)

  return (
    <Controller
      control={control}
      name="scope"
      render={({ field }) => (
        <Select
          {...field}
          sx={{
            "& .MuiSelect-select": {
              display: "flex",
              alignItems: "center",
              gap: 1,
            },
          }}
          variant="outlined"
          fullWidth
          size="small"
        >
          <MenuItem sx={{ gap: 1 }} value="auto">
            <CqsScopeIcon icon={<AutoMode />} />
            Auto
          </MenuItem>
          {datasets.value.map(ds => (
            <MenuItem sx={{ gap: 1 }} key={ds.id} value={ds.id}>
              <CqsScopeIcon icon={ds.icon} />
              {ds.label}
            </MenuItem>
          ))}
        </Select>
      )}
    />
  )
}

export { CqsScopeSelector }
